import React, { Component } from 'react';
import './table.css';

class Table extends Component {
    state = {
        tasks: null,
        students: null,
        mentor: null,
    };

    getTasksList() {
        fetch('http://localhost:3000/tasks')
            .then(response => {
                if (response.ok) return response.json();
                else console.log(mistake + response.status);
            })
            .then(json => {
                this.state.tasks = Array.from(json);
            });
    }

    getStudents() {
        fetch('http://localhost:3000/students')
            .then(response => {
                if (response.ok) return response.json();
            })
            .then(json => {
                this.state.students = Array.from(json);
            });
    }

    addStyleToCell(data, score) {
        const dataCopy = data.toLowerCase();
        if (dataCopy === 'checked' && score) return 'green';
        else if (dataCopy === 'checked' && !score) return 'darkRed';
        else if (dataCopy === 'in progress') return 'yellow';
        else if (dataCopy === 'checking') return 'red';
        else if (dataCopy === 'todo') return 'grey';
    }

    // componentWillMount() {
    //     this.getTasksList();
    //     this.getStudents();
    // }

    render() {
        this.getTasksList();
        this.getStudents();

        const { mentor } = this.props;
        const { tasks } = this.state;
        const { students } = this.state;

        console.log(mentor);

        return (
            <div className='table-component'>
                {mentor && tasks && students && (
                    <table id='table' className='table'>
                        <tr>
                            <th />
                            {mentor.students.map(student => (
                                <th>
                                    <a
                                        target='_blank'
                                        href={`https://github.com/${student}`}
                                    >
                                        {student}
                                    </a>
                                </th>
                            ))}
                        </tr>

                        {tasks.map(task => {
                            return (
                                <tr>
                                    <td>
                                        <a
                                            target='_blank'
                                            href={`${task.link}`}
                                        >
                                            {task.task}
                                        </a>
                                    </td>

                                    {mentor.students.map(student => {
                                        const currentStudent = students.find(
                                            st =>
                                                st.gitHub.toLowerCase() ===
                                                student.toLowerCase(),
                                        );

                                        const currentTaskScore =
                                            currentStudent[task.task.trim()];

                                        return (
                                            <td
                                                className={this.addStyleToCell(
                                                    task.status,
                                                    currentTaskScore,
                                                )}
                                            >
                                                {currentTaskScore}
                                            </td>
                                        );
                                    })}
                                </tr>
                            );
                        })}
                    </table>
                )}
            </div>
        );
    }
}

export default Table;
