import React, { Component } from 'react';
import Select from 'react-select';

class SelectComp extends Component {
    state = {
        selectedOption: null,
        savedMentor: null,
        mentors: [],
    };

    componentWillMount() {
        const savedMentor = localStorage.getItem('mentor');
        this.setState({ savedMentor: savedMentor });
        this.makeRequestAllMentors();
    }

    componentDidMount() {
        this.makeRequest(this.state.savedMentor);
    }

    handleChange = selectedOption => {
        this.setState({ selectedOption });
        localStorage.setItem('mentor', selectedOption.value);
        this.makeRequest(selectedOption.value);
    };

    makeRequestAllMentors() {
        fetch(`http://localhost:3000/mentors`)
            .then(response => {
                if (response.ok) return response.json();
            })
            .then(mentors => {
                const mentorsArr = Array.from(mentors);
                this.setState({ mentors: mentorsArr });
            });
    }

    makeRequest(data) {
        if (data) {
            fetch(`http://localhost:3000/${data}`)
                .then(response => {
                    if (response.ok) return response.json();
                })
                .then(mentor => {
                    this.props.updateData(mentor);
                });
        }
    }

    render() {
        let options = [];
        const { mentors } = this.state;
        if (typeof mentors !== 'undefined') {
            const mentorsGithubArray = mentors.map(mentor =>
                mentor.github.slice(19).replace(/\//g, ''),
            );

            options = mentorsGithubArray.map(mentor => {
                return { value: mentor, label: mentor };
            });
        }
        return (
            <Select
                options={options}
                onChange={this.handleChange}
                placeholder={this.state.savedMentor || 'Select...'}
                defaultInputValue={this.state.savedMentor || ''}
                autoFocus
            />
        );
    }
}

export default SelectComp;
